.result{
    height: 100vh;
    display: grid;
    place-items: center;
    text-align: center;
    background-image: url("https://images.pexels.com/photos/1643409/pexels-photo-1643409.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;

}

.result__box{
    padding:10%;
    text-align: center;
    padding-top: 50px;
    padding-bottom: 50px;
}

.result__box >h1{
    color: white;
}
.result__box> p{
    color: white;
}

.result__box>.MuiButton-contained{
    padding: 10px 40px;
    
}