@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap');

.inputBox{
    display: grid;
    place-items: center;
    height: 100vh;
    background-image: url("https://images.hdqwalls.com/wallpapers/dusk-mountains-4k-ma.jpg");
    background-size: cover;
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
}

.inputBox__field{
    padding: 10px;
    text-align: center;
    padding-top:0 ;
    padding-bottom: 15px;
    margin: 20px;
}

.inputBox__field >h1{
    font-family: 'Montserrat', sans-serif;
    margin-top: 10px;
    color: white;
}

.inputBox__field >p{
    padding: 20px;
    color: white;
}

.inputBox__field > img{
    width: 10%;
    padding-bottom: 40px;
    padding-top: 30px;
}

.inputBox__field>form> button{
    margin-top: 30px;
    text-transform: inherit !important;
    background-color: #0a8d48 !important;
    color: white;
    padding: 10px 40px;
}

.MuiInputBase-root >input{
    color:white;
}

.MuiInputBase-root >input::placeholder{
    color: white;
}